<template>
  <div>
    <div class="rkl-pelaporan">
      <h1 class="content-header">{{ this.$route.name }}</h1>
      <div v-if="$route.params.render == 'daftar'" class="content">
        <base-header class="pb-6" type="primary">
          <div class="row">
            <div class="col-lg-12 py-2 col-5 text-right">
              <el-select
                v-show="$store.getters.isAdmin"
                v-model="uk_id"
                size="mini"
                placeholder="Pilih Unit Kerja"
                class="mr-2"
                @change="getTable(uk_id)"
              >
                <el-option
                  v-for="option in cabang"
                  :key="option.label"
                  :label="option.label"
                  :value="option.value"
                ></el-option>
              </el-select>
              <el-select
                v-model="periode"
                size="mini"
                placeholder="Pilih Periode"
                class="mr-2"
                @change="changePeriode(periode)"
              >
                <el-option
                  v-for="option in listPeriode"
                  :key="option.label"
                  :label="option.label"
                  :value="option.value"
                ></el-option>
              </el-select>

              <base-button size="sm" type="neutral" @click="showForm"
                >Tambah</base-button
              >
            </div>
          </div>
        </base-header>
        <div class="container-fluid mt--6">
          <div>
            <div class="card">
              <div class="card-header">
                <h2 class="m-0">
                  Daftar Pelaporan Rencana Pengelolaan Lingkungan
                </h2>
              </div>
              <div>
                <el-table
                  :data="queriedData"
                  row-key="id"
                  header-row-class-name="thead-light"
                  @sort-change="sortChange"
                  @row-click="toDetail"
                >
                  <el-table-column
                    sortable
                    v-for="column in tableColumns"
                    :key="column.label"
                    v-bind="column"
                  ></el-table-column>
                  <el-table-column
                    label="Nilai Tercapai"
                    min-width="160"
                    align="center"
                    sortable
                  >
                    <template v-slot="{ row }">
                      <div class="cell">
                        {{ row.NILAI_PRKL[0] }} / {{ row.NILAI_PRKL[1] }}
                      </div>
                    </template>
                  </el-table-column>
                </el-table>
              </div>
              <div
                slot="footer"
                class="col-12 px-4 py-2 d-flex justify-content-center justify-content-sm-between flex-wrap"
              >
                <div class="d-flex align-items-center">
                  <p class="card-category mb-0 mr-2">Menampilkan</p>
                  <div style="width: 80px">
                    <el-select
                      class="select-primary pagination-select"
                      v-model="pagination.perPage"
                      placeholder="Per page"
                      size="mini"
                    >
                      <el-option
                        class="select-primary"
                        v-for="item in pagination.perPageOptions"
                        :key="item"
                        :label="item"
                        :value="item"
                      ></el-option>
                    </el-select>
                  </div>
                  <p class="card-category m-0 ml-2">
                    Tampil {{ from + 1 }} sampai {{ to }} dari {{ total }} data
                    <span v-if="selectedRows.length"
                      >&nbsp; &nbsp; {{ selectedRows.length }} rows
                      selected</span
                    >
                  </p>
                </div>
                <base-pagination
                  class="pagination-no-border my-3"
                  v-model="pagination.currentPage"
                  :per-page="pagination.perPage"
                  :total="total"
                ></base-pagination>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div v-if="$route.params.render == 'form'">
        <base-header class="pt-4 pb-6" type="primary"></base-header>
        <div class="container-fluid mt--6">
          <div class="card border-0">
            <div class="card-header">
              <h2>Form Pelaporan Rencana Pengelolaan Lingkungan</h2>
            </div>
            <div class="card-body">
              <base-input
                v-show="$store.getters.isAdmin"
                label="Unit Kerja"
                class="form-group"
              >
                <el-select
                  placeholder="Pilih Unit Kerja"
                  v-model="uk_id"
                  @change="getIzinLingkungan"
                >
                  <el-option
                    v-for="option in dataShift(cabang)"
                    :key="option.label"
                    :label="option.label"
                    :value="option.value"
                  ></el-option>
                </el-select>
              </base-input>
              <base-input label="Izin Lingkungan" class="form-group">
                <el-select
                  placeholder="Pilih Izin Lingkungan"
                  v-model="tempIL.label"
                  @change="getMatriksDetail()"
                >
                  <el-option
                    v-for="option in listIL"
                    :key="option.label"
                    :label="option.label"
                    :value="option.value"
                  ></el-option>
                </el-select>
              </base-input>
              <base-input
                type="text"
                label="Periode"
                placeholder="Periode"
                class="form-group"
              >
                <el-select
                  placeholder="Pilih Periode"
                  v-model="formPelaporanRKL.periode"
                >
                  <el-option
                    v-for="option in dataShift(listPeriode)"
                    :key="option.label"
                    :label="option.label"
                    :value="option.value"
                  ></el-option>
                </el-select>
              </base-input>
              <base-input
                v-model="formPelaporanRKL.no"
                type="string"
                label="Nomor Izin Lingkungan"
                class="form-group"
                disabled
              ></base-input>
              <base-input
                v-model="formPelaporanRKL.date"
                label="Tanggal Izin Lingkungan"
                class="form-group"
                disabled
              >
                <!-- <flat-picker
              slot-scope="{ focus, blur }"
              @on-open="focus"
              @on-close="blur"
              :config="{ allowInput: true }"
              :placeholder="formPelaporanRKL.date"
              class="form-control datepicker"
              v-model="formPelaporanRKL.date"
                ></flat-picker>-->
              </base-input>
              <base-input label="Matriks">
                <el-select
                  @change="getMatriks"
                  v-model="formPelaporanRKL.matriksrkl_id"
                  class="form-group mb-3"
                  :placeholder="matiksHolder"
                >
                  <el-option
                    v-for="option in optionsMatriks"
                    :value="option.value"
                    :label="option.label"
                    :key="option.value"
                  ></el-option>
                </el-select>
              </base-input>
              <div v-if="jumlahUpaya > 0" class="col-12">
                <div class="row flex align-items-stretch">
                  <div class="col-10 p-0">
                    <div class="col-12 p-0">
                      <div class="row">
                        <div class="col-4">
                          <h5>Upaya Pengelolaan Lingkungan</h5>
                        </div>
                        <div class="col-4">
                          <h5>Pelaksanaan RKL</h5>
                        </div>
                        <div class="col-4">
                          <h5>Hasil yang Dicapai</h5>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div class="col-2">
                    <h5>Ketercapaian</h5>
                  </div>
                </div>
              </div>
              <div class="col-12" v-for="index in jumlahUpaya" :key="index">
                <div class="row flex align-items-stretch">
                  <div class="col-10 p-0">
                    <div class="col-12 p-0">
                      <div class="row">
                        <div class="col-4">
                          <span
                            class="form-control mb-3 overflow-auto force-inherit"
                            style="height:144px"
                            v-html="detailUpaya[index - 1]"
                          ></span>
                        </div>
                        <div class="col-4">
                          <textarea
                            v-model="loopUpaya.data[index - 1]"
                            class="form-control mb-3 text-dark"
                            resize="none"
                            style="height:144px"
                          ></textarea>
                        </div>
                        <div class="col-4">
                          <textarea
                            v-model="loopUpaya.datahasil[index - 1]"
                            class="form-control mb-3 text-dark"
                            resize="none"
                            style="height:144px"
                          ></textarea>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div class="col-2">
                    <base-switch
                      v-model="loopUpaya.datacapai[index - 1]"
                      class="mb-6"
                    ></base-switch>
                  </div>
                </div>
              </div>

              <div class="col-lg-12 col-5 px-0 pt-4 text-right">
                <base-button size="md" type="danger" @click="hideForm"
                  >Kembali</base-button
                >
                <base-button
                  v-if="!isUpdate"
                  size="md"
                  type="primary"
                  @click="submitForm"
                  >Simpan</base-button
                >
                <base-button
                  v-if="isUpdate"
                  size="md"
                  type="primary"
                  @click="submitFormUpdate"
                  >Update</base-button
                >
              </div>
            </div>
          </div>
        </div>
      </div>
      <div v-if="$route.params.render == 'detail'">
        <base-header class="pb-6" type="primary">
          <div class="row">
            <div class="col-lg-12 py-2 col-5 text-right">
              <!-- <el-select
                v-show="$store.getters.isAdmin"
                v-model="uk_id"
                size="mini"
                placeholder="Unit Kerja"
                class="mr-2"
                @change="getTable(uk_id)"
              >
                <el-option label="All" value="9999"></el-option>
                <el-option
                  v-for="option in cabang"
                  :key="option.label"
                  :label="option.label"
                  :value="option.value"
                ></el-option>
              </el-select> -->
              <base-button size="sm" type="neutral" @click="showPeta">{{
                petaButton
              }}</base-button>
              <base-button size="sm" type="neutral" @click="toUpdateForm"
                >Ubah</base-button
              >
              <base-button
                size="sm"
                type="danger"
                @click="deleteData(tempUpdateRow.DATARKL_ID)"
                >Hapus</base-button
              >
            </div>
          </div>
        </base-header>
        <div class="container-fluid mt--6">
          <div v-if="isPeta" class="card border-0">
            <div class="card-header">
              <h2>
                {{ tempUpdateRow.MRKL_KEGIATAN }} -
                {{ tempUpdateRow.IL_NOMORIZIN }} ({{ tempUpdateRow.IL_DATE }})
              </h2>
            </div>
            <div class="card-body d-flex justify-content-center w-100">
              <img class="img-fluid" style="height: 480px" :src="petaLink" />
            </div>
          </div>
          <div>
            <card
              class="no-border-card"
              body-classes="p-0"
              footer-classes="pb-2"
            >
              <div class="card-header">
                <h2>{{ tempUpdateRow.MRKL_KEGIATAN }}</h2>
              </div>
              <div>
                <el-table
                  :data="tableData2"
                  row-key="id"
                  header-row-class-name="thead-light"
                >
                  <el-table-column label="Upaya Pengelolaan lingkungan">
                    <template slot-scope="upaya">
                      <span
                        class="force-inherit"
                        v-html="upaya.row.dataupaya"
                      ></span>
                    </template>
                  </el-table-column>
                  <el-table-column label="Pelaksanaan RKL">
                    <template slot-scope="data">
                      <span v-html="data.row.data"></span>
                    </template>
                  </el-table-column>
                  <el-table-column label="Hasil yang Dicapai">
                    <template slot-scope="hasil">
                      <span v-html="hasil.row.datahasil"></span>
                    </template>
                  </el-table-column>
                  <el-table-column label="Ketercapaian">
                    <template slot-scope="capai">
                      <span v-html="capai.row.datacapai"></span>
                    </template>
                  </el-table-column>
                </el-table>
              </div>
            </card>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import "sweetalert2/dist/sweetalert2.css";
import HtmlEditor from "@/components/Inputs/HtmlEditor";
import DropzoneFileUpload from "@/components/Inputs/DropzoneFileUpload";
import flatPicker from "vue-flatpickr-component";
import "flatpickr/dist/flatpickr.css";
import { Table, TableColumn, Select, Option } from "element-ui";
import { BasePagination, BaseInput, BaseSwitch } from "@/components";
import clientPaginationMixin from "@/components/clientPaginationMixin";
import swal from "sweetalert2";
import users from "./users2";
import axios from "axios";
import moment from "moment";
import cabang from "../../cabang";
import listPeriode from "@/views/Pages/listPeriode.js";

export default {
  mixins: [clientPaginationMixin],
  components: {
    listPeriode,
    HtmlEditor,
    BasePagination,
    BaseInput,
    BaseSwitch,
    flatPicker,
    DropzoneFileUpload,
    [Select.name]: Select,
    [Option.name]: Option,
    [Table.name]: Table,
    [TableColumn.name]: TableColumn
  },
  data() {
    return {
      listIL: [],
      tempIL: {},
      tableDataOri: [],
      periode: "",
      listPeriode,
      matiksHolder: "Pilih Matriks RKL",
      datarkl_id: 0,
      tempUpdateRow: {},
      cabang,
      selects: {
        unitId: null
      },
      petaLink: "",
      isPeta: false,
      loopUpaya: {
        data: [],
        datahasil: [],
        datacapai: []
      },
      ketercapaian: [],
      testHTML: "<p>test</p>",
      detailUpaya: [],
      jumlahUpaya: 0,
      optionsMatriks: {},
      uk_id: null,
      il_id: null,
      totalUpaya: 0,
      formPelaporanRKL: {
        periode: "",
        no: "",
        date: null,
        matriksrkl_id: "",
        datacapai: "",
        datahasil: "",
        data: ""
      },
      dates: {
        simple: new Date("2020-10-10"),
        range: "2019-04-19 to 2019-04-24"
      },
      isTable: true,
      isForm: false,
      isDetail: false,
      isUpdate: false,
      propsToSearch: ["name", "email", "age"],
      tableColumns2: [
        {
          align: "center",
          prop: "IL_DATE",
          label: "Upaya Pengelolaan lingkungan",
          minWidth: 150
        },
        {
          prop: "UNITKERJA_ID",
          label: "Pelaksanaan RKL",
          minWidth: 150
        },
        {
          prop: "IL_NOMORIZIN",
          label: "Hasil yang Dicapai",
          minWidth: 150
        },
        {
          prop: "MRKL_TAHAPAN",
          label: "Ketercapaian",
          minWidth: 150
        }
      ],
      tableColumns: [
        {
          align: "center",
          prop: "IL_DATE",
          label: "Tanggal",
          minWidth: 150
        },
        {
          prop: "UK_NAME",
          label: "Unit Kerja",
          minWidth: 150
        },
        {
          prop: "DATAPERIODE",
          label: "Periode",
          minWidth: 150
        },
        {
          prop: "IL_NAME",
          label: "Izin Lingkungan",
          minWidth: 150
        },
        {
          prop: "MRKL_KEGIATAN",
          label: "Matriks",
          minWidth: 150
        }
      ],
      tableData: [],
      tableData2: [],
      selectedRows: [],
      petaButton: "Tampilkan Peta",
      title: "Daftar Pelaporan Rencana Pengelolaan Lingkungan"
    };
  },
  created() {
    this.getTable(this.$store.getters.getUKId);
    if (!this.$store.getters.isAdmin) {
      this.uk_id = this.$store.getters.getUKId;
      this.getIzinLingkungan();
    }
    // this.getListPeriode();
  },
  methods: {
    dataShift(data) {
      let tempData = [];
      for (let i = 1; i < data.length; i++) {
        tempData.push(data[i]);
      }
      return tempData;
    },
    changePeriode(value) {
      if (value == null) return (this.tableData = this.tableDataOri);
      let tempTable = this.tableDataOri.filter(
        item => item.DATAPERIODE == value
      );
      this.tableData = tempTable;
    },
    // getListPeriode() {
    //   axios
    //     .get("http://45.127.133.75:3000/api/iems/list-periode")
    //     .then(res => {
    //       console.log(res.data);
    //       let tempData = [];
    //       res.data.forEach(item => {
    //         tempData.push({
    //           name: item.PERIODE,
    //           value: item.PERIODE
    //         });
    //       });
    //       console.log(tempData);
    //       this.listPeriode = tempData;
    //     })
    //     .catch(err => {
    //       console.log(err);
    //     });
    // },
    deleteData(id) {
      swal
        .fire({
          title: "Apakah anda yakin untuk menghapus?",
          showCancelButton: true,
          confirmButtonText: `Hapus`,
          cancelButtonText: `Kembali`,
          confirmButtonColor: "#d33",
          cancelButtonColor: "#3085d6",
          icon: "question"
        })
        .then(result => {
          /* Read more about isConfirmed, isDenied below */
          if (result.isConfirmed) {
            axios
              .delete("http://45.127.133.75:3000/api/iems/mrkl-rep/" + id)
              .then(res => {
                swal
                  .fire({
                    title: `Success`,
                    text: "Berhasil terhapus!",
                    buttonsStyling: false,
                    confirmButtonClass: "btn btn-success",
                    icon: "success"
                  })
                  .then(result => {
                    this.getTable(this.$store.getters.getUKId);
                    if (!this.$store.getters.isAdmin) {
                      this.uk_id = this.$store.getters.getUKId;
                      this.getIzinLingkungan();
                    }
                    this.$router.push("daftar");
                  });
              })
              .catch(err => {
                console.log(err);
                this.swalError("Terjadi Kesalahan " + err);
              });
          }
        });
    },
    swalWarning(message) {
      swal.fire({
        title: `Warning`,
        text: message,
        buttonsStyling: false,
        confirmButtonClass: "btn btn-warning",
        icon: "warning"
      });
    },
    swalError(message) {
      swal.fire({
        title: `Error!`,
        text: message,
        buttonsStyling: false,
        confirmButtonClass: "btn btn-danger",
        icon: "error"
      });
    },
    toUpdateForm() {
      this.isUpdate = true;
      this.uk_id = this.tempUpdateRow.UNITKERJA_ID;
      this.tempIL.label = this.tempUpdateRow.IL_NAME;
      // this.tempIL.label.no_izin = this.tempUpdateRow.IL_NOMORIZIN
      // this.tempIL.label.date = this.tempUpdateRow.IL_NAME;
      this.getIzinLingkungan();
      // this.getMatriksDetail();
      this.formPelaporanRKL.matriksrkl_id = this.tempUpdateRow.MATRIKSRKL_ID;
      this.matiksHolder = this.tempUpdateRow.MRKL_KEGIATAN;
      this.datarkl_id = this.tempUpdateRow.DATARKL_ID;
      this.il_id = this.tempUpdateRow.IL_ID;
      this.detailUpaya = JSON.parse(this.tempUpdateRow.MRKL_UPENGELOLAAN);
      this.jumlahUpaya = this.detailUpaya.length;
      this.loopUpaya.data = JSON.parse(this.tempUpdateRow.DATA);
      this.loopUpaya.datacapai = JSON.parse(this.tempUpdateRow.DATACAPAI);
      this.loopUpaya.datahasil = JSON.parse(this.tempUpdateRow.DATAHASIL);
      this.formPelaporanRKL.no = this.tempUpdateRow.IL_NOMORIZIN;
      this.formPelaporanRKL.periode = this.tempUpdateRow.DATAPERIODE;
      this.formPelaporanRKL.date = this.tempUpdateRow.IL_DATE;
      axios
        .get("http://45.127.133.75:3000/api/iems/rkl/9999?ilid=" + this.il_id)
        .then(res => {
          let tempMatriks = res.data;
          let listMatriks = [];
          tempMatriks.forEach(element => {
            listMatriks.push({
              label: element.MRKL_KEGIATAN,
              value: element.MATRIKSRKL_ID
            });
          });
          this.optionsMatriks = listMatriks;
        });

      this.$router.push("form");
    },
    submitFormUpdate() {
      let updateData = this.formPelaporanRKL;
      updateData.data = JSON.stringify(this.loopUpaya.data);
      updateData.datacapai = JSON.stringify(this.loopUpaya.datacapai);
      updateData.datahasil = JSON.stringify(this.loopUpaya.datahasil);
      axios
        .put(
          "http://45.127.133.75:3000/api/iems/rkl-report/" +
            this.tempUpdateRow.DATARKL_ID,
          updateData
        )
        .then(res => {
          swal
            .fire({
              title: `Success`,
              text: "Update Form berhasil tersimpan!",
              buttonsStyling: false,
              confirmButtonClass: "btn btn-success",
              icon: "success"
            })
            .then(result => {
              this.isUpdate = false;
              this.getTable(this.uk_id);
              this.$router.push("daftar");
            });
        })
        .catch(err => {
          console.log(err);
          swal.fire({
            title: `Warning`,
            text: "Update Form tidak tersimpan!!!",
            buttonsStyling: false,
            confirmButtonClass: "btn btn-warning",
            icon: "warning"
          });
        });
    },
    encodeDetail(data) {
      // data = data.split("&%");
      // data.pop();
      return JSON.parse(data);
    },
    showPeta() {
      if (this.isPeta === false) {
        this.isPeta = true;
        this.petaButton = "Sembunyikan Peta";
      } else {
        this.isPeta = false;
        this.petaButton = "Tampilkan Peta";
      }
    },
    toDetail(row, col, event) {
      this.tempUpdateRow = row;
      console.log(this.tempUpdateRow);
      this.petaLink = row.IL_MAPRKL_LINK;
      this.title = row.MRKL_KEGIATAN;
      let tempTable2 = [];
      let tempDataUpaya = this.encodeDetail(row.MRKL_UPENGELOLAAN);
      let tempData = this.encodeDetail(row.DATA);
      let tempDataHasil = this.encodeDetail(row.DATAHASIL);
      let tempDataCapai = this.encodeDetail(row.DATACAPAI);
      for (let i = 0; i < tempDataUpaya.length; i++) {
        if (tempDataCapai[i] === true) {
          tempDataCapai[i] = "Tercapai";
        } else {
          tempDataCapai[i] = "Tidak Tercapai";
        }
        tempTable2.push({
          dataupaya: tempDataUpaya[i],
          data: tempData[i],
          datahasil: tempDataHasil[i],
          datacapai: tempDataCapai[i]
        });
      }
      this.tableData2 = tempTable2;
      this.$router.push("detail");
    },
    toFormatString(data) {
      // let tempData = [];
      // for (let i = 0; i < data.length; i++) {
      //   tempData = tempData + data[i] + "&%";
      // }
      return JSON.stringify(data);
    },
    submitForm() {
      let tempCapai = this.loopUpaya.datacapai;
      for (let i = 0; i < this.jumlahUpaya; i++) {
        if (tempCapai[i] !== true) {
          tempCapai[i] = false;
        }
      }
      this.formPelaporanRKL.data = this.toFormatString(this.loopUpaya.data);
      this.formPelaporanRKL.datahasil = this.toFormatString(
        this.loopUpaya.datahasil
      );
      this.formPelaporanRKL.datacapai = this.toFormatString(
        this.loopUpaya.datacapai
      );
      axios
        .post(
          "http://45.127.133.75:3000/api/iems/rkl-report-new/",
          this.formPelaporanRKL
        )
        .then(res => {
          swal
            .fire({
              title: `Success`,
              text: "Form berhasil tersimpan!",
              buttonsStyling: false,
              confirmButtonClass: "btn btn-success",
              icon: "success"
            })
            .then(result => {
              this.isUpdate = false;
              this.getTable(this.uk_id);
              this.$router.push("daftar");
            });
        })
        .catch(err => {
          console.log(err);
          swal.fire({
            title: `Warning`,
            text: "Form tidak tersimpan!!!",
            buttonsStyling: false,
            confirmButtonClass: "btn btn-warning",
            icon: "warning"
          });
        });
    },
    getTable(data) {
      axios
        .get("http://45.127.133.75:3000/api/iems/rkl-report/" + data)
        .then(res => {
          res.data.forEach(el => {
            let tempNilai = JSON.parse(el.DATACAPAI);
            let tempTercapai = tempNilai.filter(item => item == true);
            el.IL_DATE = moment.unix(el.IL_DATE).format("L");
            el.NILAI_PRKL = [tempTercapai.length, tempNilai.length];
          });
          console.log(res.data);
          this.tableData = res.data;
          this.tableDataOri = res.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    getMatriks() {
      axios
        .get(
          "http://45.127.133.75:3000/api/iems/rkl-matriks/" +
            this.formPelaporanRKL.matriksrkl_id
        )
        .then(res => {
          let tempUp = res.data[0].MRKL_UPENGELOLAAN;
          this.detailUpaya = JSON.parse(tempUp);
          // this.detailUpaya = tempUp.split("&%");
          // this.detailUpaya.pop();
          this.jumlahUpaya = this.detailUpaya.length;
          // this.loopUpaya.datacapai.forEach((data) => {
          //   data = false;
          // });
          // for (let i = 0; i < this.detailUpaya.length; i++) {
          //   const a = this.detailUpaya[i];
          //   this.detailUpaya[i] = a.replace("&%", "");
          // }
        });
    },
    getIzinLingkungan() {
      axios
        .get("http://45.127.133.75:3000/api/iems/il/" + this.uk_id)
        .then(res => {
          console.log(res.data);
          let tempDataArray = [];
          res.data.forEach(element => {
            tempDataArray.push({
              label: element.IL_NAME,
              value: {
                id: element.IL_ID,
                no_izin: element.IL_NOMORIZIN,
                date_izin: element.IL_DATE
              }
            });
          });
          this.listIL = tempDataArray;
          console.log(this.listIL);
        })
        .catch(err => {
          console.log(err);
        });
    },
    getMatriksDetail() {
      this.formPelaporanRKL.no = this.tempIL.label.no_izin;
      this.formPelaporanRKL.date = moment
        .unix(this.tempIL.label.date_izin)
        .format("L");
      axios
        .get(
          "http://45.127.133.75:3000/api/iems/rkl/9999?ilid=" +
            this.tempIL.label.id
        )
        .then(res => {
          let tempMatriks = res.data;
          let listMatriks = [];
          tempMatriks.forEach(element => {
            listMatriks.push({
              label: element.MRKL_KEGIATAN,
              value: element.MATRIKSRKL_ID
            });
          });
          this.optionsMatriks = listMatriks;
        });
    },
    getILID() {
      axios
        .get("http://45.127.133.75:3000/api/iems/il/" + this.uk_id)
        .then(res => {
          if (!res.data[0]) {
            swal
              .fire({
                title: `Unit Kerja ini tidak memiliki Izin Lingungan`,
                text: "Silakan isi Izin Lingungan Terlebuh Dahulu!",
                buttonsStyling: false,
                showCancelButton: true,
                confirmButtonText: "Ke Form Izin Lingkungan",
                cancelButtonText: "Kembali",
                confirmButtonClass: "btn btn-success",
                cancelButtonClass: "btn btn-danger",
                icon: "error"
              })
              .then(result => {
                /* Read more about isConfirmed, isDenied below */
                if (result.isConfirmed) {
                  this.$router.push("/iems/izin-lingkungan/form");
                }
              });
            return;
          }
          this.formPelaporanRKL.no = res.data[0].IL_NOMORIZIN;
          this.formPelaporanRKL.date = moment
            .unix(res.data[0].IL_DATE)
            .format("L");
          axios
            .get(
              "http://45.127.133.75:3000/api/iems/rkl/9999?ilid=" +
                res.data[0].IL_ID
            )
            .then(resp => {
              console.log(resp.data);
              if (!resp.data[0]) {
                swal
                  .fire({
                    title: `Unit Kerja ini tidak memiliki Matiks RKL`,
                    text: "Silakan isi Matiks RKL Terlebuh Dahulu!",
                    buttonsStyling: false,
                    showCancelButton: true,
                    confirmButtonText: "Ke Form Matiks RKL",
                    cancelButtonText: "Kembali",
                    confirmButtonClass: "btn btn-success",
                    cancelButtonClass: "btn btn-danger",
                    icon: "error"
                  })
                  .then(result => {
                    /* Read more about isConfirmed, isDenied below */
                    if (result.isConfirmed) {
                      this.$router.push("/iems/rkl/matriks/form");
                    }
                  });
                return;
              }
              let tempOpt = [];
              for (let i = 0; i < resp.data.length; i++) {
                tempOpt.push({
                  value: resp.data[i].MATRIKSRKL_ID,
                  label: resp.data[i].MRKL_KEGIATAN
                });
              }
              this.optionsMatriks = tempOpt;
            });
          this.formPelaporanRKL.matriksrkl_id = res.data[0].MATRIKSRKL_ID;
        })
        .catch(err => {
          console.log(err);
        });
    },
    showDetail() {
      this.isForm = false;
      this.isTable = false;
      this.isDetail = true;
    },
    showForm() {
      this.getIzinLingkungan();
      this.$router.push("form");
    },
    hideForm() {
      this.uk_id = "";
      this.periode = "";
      this.$router.push("daftar");
      if (this.$store.getters.isAdmin) {
        this.uk_id = null;
        this.getTable(9999);
      }
    }
  }
};
</script>
